import React, { Component, useCallback } from 'react';
import {Route} from 'react-router-dom';
import axios from 'axios'
import Callback from './Callback';
import { Redirect } from 'react-router-dom'
import {
    BrowserRouter as Router,
    Switch,
    Link
  } from "react-router-dom";

import Cookies from 'universal-cookie';
import Search from 'react-search'
import './Problem.css'
import Timer from 'react-compound-timer';
import ReactHtmlParser from 'react-html-parser'; 
import {refresh_token} from './Api'
class Problem extends React.Component
{
  constructor(props)
{
super(props);
const cookies = new Cookies();
const list=[],codelist=[],finallist=[],problemlist=[];
this.state = {
 list,
 codelist,
 problemlist,
 finallist,
token:"ht",
taxt:'',
object:null,
clicked:"A",
suggestions:[],
show:false,
problemname:'',
var:'A',
starttime:"",
lasttime:"",
currenttime:"" ,
starttime1:"",
lasttime1:"",
currenttime1:"" ,
bannerfile:'',
object1:null,
solved:null,
ram:[],
solved1:null,
username1:'',
length:20,


}
this.showproblem=this.showproblem.bind(this);
this.showranking=this.showranking.bind(this);

}
sleep=(miliseconds) =>{
  var currentTime = new Date().getTime();

  while (currentTime + miliseconds >= new Date().getTime()) {
  }
}




showproblem= (e) => {
 
    const cookies = new Cookies();

  var token1=cookies.get('username');
  var value=cookies.get('contestcode');

  axios.get(`https://api.codechef.com/contests/${value}?fields=&sortBy=&sortOrder="asc"`,
  { headers: {'Content-Type': 'application/json',
        'Accept': 'application/json',
      'Authorization': 'Bearer '+ token1 } }
) .then(res => {console.log(res);   
  this.setState( {problemlist:res.data.result.data.content.problemsList,starttime:res.data.result.data.content.startDate,lasttime:res.data.result.data.content.endDate,object1:res.data.result.data.content})})
.catch(function (error)
{
  console.log(error.message);
});
  
console.log(this.state.object1);


  }
showtime=()=>
{
  if(this.state.lasttime1<this.state.currenttime1)
  {
    return <div className="ranklist1"><p className="a">Content Ends</p></div>
  }
  if(this.state.lasttime1>this.state.currenttime1 && this.state.starttime1>this.state.currenttime1 )
  {
    return <div >
      <div className="ranklist3"><p className="a">Content Starts In</p></div>
      <div><Timer
    initialTime={Number( this.state.starttime1)-Number(this.state.currenttime1)}
    direction="backward"
>
    {() => (
        <React.Fragment>
          <table cellpadding="10" className="bb1" >
            <tr>
            <td className="b"><b>Days</b></td>  
            <td className="b"><b>Hrs</b></td> 
            <td className="b"><b>Min</b></td> 
            <td className="b"><b>Sec</b></td>
            </tr>
            <tr>
            <td className="b11"><i><Timer.Days /> </i></td>  
            <td className="b11"><i><Timer.Hours /></i></td> 
            <td className="b11"><i><Timer.Minutes /></i></td> 
            <td className="b11"><i><Timer.Seconds /></i></td>
           
            </tr>
            </table>
        </React.Fragment>
    )}
</Timer></div>
    </div>
  }
  if(this.state.lasttime1>this.state.currenttime1 && this.state.starttime1<this.state.currenttime1 )
  {
    return <div >
      <div className="ranklist3"><p className="a">Content Ends In</p></div>
      <div><Timer
    initialTime={Number( this.state.lasttime1)-Number(this.state.currenttime1)}
    direction="backward"
>
    {() => (
        <React.Fragment>
          <table cellpadding="10" className="bb1" >
            <tr>
            <td className="b"><b>Days</b></td>  
            <td className="b"><b>Hrs</b></td> 
            <td className="b"><b>Min</b></td> 
            <td className="b"><b>Sec</b></td>
            </tr>
            <tr>
            <td className="b11"><i><Timer.Days /> </i></td>  
            <td className="b11"><i><Timer.Hours /></i></td> 
            <td className="b11"><i><Timer.Minutes /></i></td> 
            <td className="b11"><i><Timer.Seconds /></i></td>
           
            </tr>
            </table>
        </React.Fragment>
    )}
</Timer></div>
    </div>
  }
 
 
}

  
  converttomili=()=>
{
var starttime=this.state.starttime.toString();
var date = new Date(starttime); // some mock date
var milliseconds = date.getTime();
var endtime=this.state.lasttime.toString();
var date1 = new Date(endtime); // some mock date
var milliseconds1 = date1.getTime();
console.log(milliseconds);
console.log(milliseconds1);
var today = Math.round((new Date()).getTime() );
console.log(today);
this.setState({starttime1:milliseconds,lasttime1:milliseconds1,currenttime1:today,clicked:"AAA"});
}

  showranking = () => {
    this.setState(() => ({clicked:"Ranking"}));
     }
     logout = (e)=>
     {
      
       console.log(this.state.clicked)
       const cookies=new Cookies();
       cookies.remove('username',{path:'/'});
       cookies.remove('refresh_token',{path:'/'});
       cookies.remove('visited',{path:'/'});
       this.setState({clicked:"logout"})
       
     }
  
 

       
  
 
     render()
  {
   
 
    const cookies = new Cookies();
 
    refresh_token();
    var value=cookies.get('contestcode');
    console.log(this.state.object1);
  let problemlisto=(this.state.problemlist.map((item)=>item.problemCode.toString()));
  const url='/'+{value}+'/problem/'
  if(this.state.clicked=="AA")
  {
      var code='/frontend/contest/problem/'+this.state.taxt.toString();

    console.log(this.state.taxt);
    return <Redirect to={'/frontend/contest/problem/'+this.state.taxt.toString()}/>
  }
  if(this.state.clicked=="Ranking")
  {
     
    return <Redirect to={'/frontend/contest/ranking/'}/>
  }
  if(this.state.clicked=="logout")
  {
    return <Redirect to="/frontend/" />
  }

    return (
      <div>
         <button onClick={this.logout} class="butoon3">Logout</button>
      <div className="allpage">
       
       {this.state.starttime=="" && this.showproblem()}
       <div className="first">
      
        {this.state.object1!=null && <img src={this.state.object1.bannerFile} alt="Coming Soon" className="image"/>}
        
       
               <table className="bb">
                    <tbody>
                        <tr className="b">
                            <th className="b"><b>Problem name</b></th>
                            <th className="b"><b>successful submission</b></th>
                            <th className="b"><b>accuracy</b></th>
                        </tr>
                        {this.state.problemlist.map((item,i)=>
                            <tr key={i} className="b">
                                <td className="b"><Link to={'/frontend/contest/problem/'+item.problemCode+'/'}>{item.problemCode}</Link> </td>
                                <td className="b">{item.successfulSubmissions}</td>
                                <td className="b">{item.accuracy.toFixed(2)}</td>
                        
                            </tr>
                        )}
                    </tbody>
               </table>
        
            </div>
            <div className="second">
                       
              {this.state.clicked=="A" && this.state.object1!=null && this.converttomili()}
               <div className="ranking"> 
                <div className="ranklist"><p className="a"><u>Contest Ranklist</u></p>
                <Link to={'/frontend/contest/ranking/'}><button onClick={this.showranking} className="butoon4">Go to Contest Rank</button></Link>
               
              
               </div></div>
               <div className="ranking1">
                 <div className>
                         {this.showtime()}
             
             
             
                </div>
                </div>
               </div> 
               
               <div className="ranking2">
              
              <Link to={"/frontend/"+cookies.get('contestcode')+"/activity/"}><button onClick={this.getinfo} className="butoon2">Click to see activity</button></Link> 
               <div className="ranklist2"><p className="a"><u>Recent Activity</u></p></div>
             
              
                </div>
                
      
         </div>
         </div>


    );
  }
}
export default Problem;