import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import axios from 'axios'
import Callback from './Callback';
import { Redirect } from 'react-router-dom'
import {
    BrowserRouter as Router,
    Switch,
    Link
  } from "react-router-dom";

import Cookies from 'universal-cookie';
import Search from 'react-search'
import './User.css';
import {refresh_token} from './Api'
class User extends React.Component {
    constructor(props)
    {
    super(props);
    const list=[];
    this.state = {
        object:null,
        problemwiseresult:[],
        clicked:"",
        institution:"",
        problemscore:[],
        problemcodes:[],
        problemlist:[],


    
    }
    
    }
    showuser= () => {
        const cookies = new Cookies();
    
      var token1=cookies.get('username');
      var value1=cookies.get('contestcode');
      var username = cookies.get('usercode1');
     
      console.log(value1);
      console.log(token1);
      let object=null;
      axios.get(`https://api.codechef.com/users/${username}?fields=`,
      { headers: {'Content-Type': 'application/json',
            'Accept': 'application/json',
          'Authorization': 'Bearer '+ token1 } }
         ) .then(res => {console.log(res);
           this.setState(
           {object:res.data.result.data.content})})
    .catch(function (error)
    {
      console.log(error.message);
    });
    let problemdetailso=this.state.problemdetails;
   
    
    
    
    
    }
    
   
    givcode = ()=>
    {
     const p=window.location.href;
     var arr = p.split('/');
     const cookies = new Cookies();
     const value=arr["5"];
     console.log(value);
     cookies.set('usercode1',value, {path: "/"})
     console.log(cookies.get('usercode1'))
    }
    logout = (e)=>
    {
     
      console.log(this.state.clicked)
      const cookies=new Cookies();
      cookies.remove('username',{path:'/'});
    cookies.remove('refresh_token',{path:'/'});
    cookies.remove('visited',{path:'/'});
      this.setState({clicked:"logout"})
      
    }
      render(){
        refresh_token();
        if(this.state.clicked=="logout")
        {
          return <Redirect to="/frontend/" />
        }
      
           if(this.state.object!=null)
           {
               return(<div>
                    <div className="allpage1">
       
      
                        <div className="first1">

                            <table className="bb1">  
                                <tbody>
                                <tr >
                                    <td className="b"><b>User name</b></td>
                                    <td  className="b">{this.state.object.username}</td>
                                    
                                </tr>
                                <tr>
                                    <td  className="b"><b>Full name</b></td>
                                    <td  className="b">{this.state.object.fullname}</td>
                                    
                                </tr>
                                <tr >
                                    <td  className="b"><b>Country</b></td>
                                    <td  className="b" >{this.state.object.country.name}</td>
                                   
                                </tr>
                                <tr >
                                    <td  className="b" ><b>State</b></td>
                                    <td  className="b">{this.state.object.state.name}</td>
                                   
                                </tr>
                                <tr >
                                    <td  className="b"><b>City</b></td>
                                    <td  className="b">{this.state.object.city.name}</td>
                                   
                                </tr>
                                <tr >
                                    <td  className="b"><b>Student/Professionals</b></td>
                                    <td  className="b">{this.state.object.occupation}</td>
                                   
                                </tr>
                                <tr >
                                    <td  className="b"><b>organization</b></td>
                                    <td  className="b">{this.state.object.organization}</td>
                                   
                                </tr>
                            </tbody>
                        </table>

                        </div>
                        <div className="second1">
                       <div className="ranking11">
                        <div className="al1">{this.state.object.ratings.allContest}</div>
                        <div className="al2">{this.state.object.band}</div>
                        <div className="al3"><u>Global Rank</u></div>
                        <div className="al4"><u>Country Rank</u></div>
                        <div className="al5">{this.state.object.rankings.allContestRanking.global}</div>
                        <div className="al6">{this.state.object.rankings.allContestRanking.country}</div>
                        <div>
                            <table className="user1"> 
                                <tr>
                                    <th className="b">Contest </th>
                                    <th className="b">Rating</th>
                                    <th className="b">Global Rank</th>
                                    <th className="b">Country Rank</th>
                                </tr>
                                <tr>
                                    <td className="b">Long Challange </td>
                                    <td className="b">{this.state.object.ratings.long}</td>
                                    <td className="b">{this.state.object.rankings.longRanking.global}</td>
                                    <td className="b">{this.state.object.rankings.longRanking.country}</td>
                                </tr>
                                <tr>
                                    <td className="b">Cook-off </td>
                                    <td className="b">{this.state.object.ratings.short}</td>
                                    <td className="b">{this.state.object.rankings.shortRanking.global}</td>
                                    <td className="b">{this.state.object.rankings.shortRanking.country}</td>
                                </tr>
                                <tr>
                                    <td className="b">LaunchTime </td>
                                    <td className="b">{this.state.object.ratings.lTime}</td>
                                    <td className="b">{this.state.object.rankings.ltimeRanking.global}</td>
                                    <td className="b">{this.state.object.rankings.ltimeRanking.country}</td>
                                </tr>
                            </table>
                        </div>
                         </div>
                        </div> 
               </div>
               </div>);
           }
          return (
            <div>
            {this.givcode()}
            {this.showuser()}
            </div>
             );
        
    
        
        
      }
    }
    export default User;
    